import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'AccessList',
  methods: {
    ...mapActions('profile', ['getAccess']),
  },
  mounted() {
    this.getAccess();
    setInterval(() => {
      this.getAccess();
    }, 60000);
  },
  computed: {
    ...mapGetters('profile', ['doorAccess', 'interlockAccess']),
  },
};
